.create_product{
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-around;
}

.imgUpload{
    width: 100%;
    height: 100%;
    border:0;
    outline: none;
    color: transparent;
    cursor: pointer;

}

.create_product form{
    max-width: 500px;
    min-width: 290px;
    width: 100%;
    margin: 15px 30px;
}
.create_product form .row{
    width: 100%;
    margin: 17px 0;
   
}

.create_product form .row label{
    font-weight: bold;
    font-size: 1em;
    font-family: sans-serif;
    margin: 5px 0 8px 0;
   
}
.create_product form input, textarea{
    width: 100%;
    min-height: 40px;
    padding: 0 5px;
    border-radius: 5px;
}
.create_product form input:focus, textarea:focus{
    border-color: #C2DCFE;
    box-shadow: 0 0 1px rgba(229, 103, 23, 0.075)inset, 0 0 8px #9AC3FE;
    outline: 0 none;
}

#fichero{
    display: none;
  }
  
  .circle{
padding: 10px;
width:100%;
background:#fff;
color:#333;
display:block;
font-family:Arial,Helvetica,sans-serif;
text-align:center;
cursor:pointer;
transition:.5s;
border:1px solid#333;
border-radius: 5px;

  }
  .circle:hover{
    border: transparent;
    background: linear-gradient(62deg, #8EC5FC 0%, #E0C3FC 100%); 
}


.create_product form button{
    width: 100%;
    height: 40px;
    background: linear-gradient(to right, #000046, #1cb5e0);  
    color: white;
    height:42px;
    text-transform: uppercase;
    letter-spacing: 2px;
    font-weight: 700;
  border-radius: 10px;
}