.product_card{
    max-width: 300px;
    overflow: hidden;
    height: 510px;
    padding: 15px;
    box-shadow: 0 0 15px #ddd;
    margin: 10px 0;
    position: relative;
}
.product_card img{
   
    display: block;
   
}
.item{
    height: 300px;
    min-width: 310px;
    padding:5px;
    object-fit: cover;
}
.item img{
    height: 100%;
    width: 100%;
    pointer-events: none;
}
.slider{
    display: flex;
    cursor: grab;
}
.slider-container{
    margin: 0 auto;
    overflow-x: hidden;
}


.product_box h2{
    width: 100%;
    text-overflow: ellipsis;
    overflow: hidden;
    white-space: nowrap;
    text-transform: capitalize;
    cursor: pointer;
    color: #323232;
}
.product_card span{
    color: crimson;
}
.product_box p{
    width: 100%;
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 3;
    height: 70px;
    overflow: hidden;
    color: #323232;
}
.row_btn{
    width: 100%;
    margin-top: 10px;
    display: flex;
    justify-content: space-between;
}
.row_btn a{
    width: 50%;
    text-align: center;
    text-transform: uppercase;
    color: white;
    font-weight: 600;
    letter-spacing: 2px;
    padding: 6px;
}

#btn_view{
    
    background: linear-gradient(to right, #000046, #1cb5e0);  
    width: 100%;
    border-radius: 5px;
    height:40px;
}
#btn_view_log{
    background: linear-gradient(0deg, #0992c4 0%, #04c872 100%);
    margin-left: 5px;
    border-radius:5px;
}

#btn_delete{
    background:linear-gradient(90deg, #ca4834 0%, #d9183f 33%, #c42b47 74%);
    margin-right: 5px;
    border-radius:5px;
}
.product_card input{
    position: absolute;
    width: 25px;
    height: 25px;
}