.create_product{
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-around;
}
.upload{
    max-width: 500px;
    height: 530px;
    width: 100%;
    border: 2px solid transparent;
    border-image: linear-gradient(to bottom, #22c1c3, #fdbb2d);
    border-image-slice: 1;
    padding: 15px;
    margin: 20px;
    position: relative;
    text-align: center;
}

.imgUpload{
    width: 100%;
    height: 100%;
    font-size:1.7rem;
    border:0;
    outline: none;
    color: transparent;
    cursor: pointer;

}

.create_product form{
    max-width: 500px;
    min-width: 290px;
    width: 100%;
    margin: 15px 30px;
}
.create_product form .row{
    width: 100%;
    margin: 15px 0;
}
.create_product form input, textarea{
    width: 100%;
    min-height: 40px;
    padding: 0 5px;
}

.create_product .rowchecked input {
    width: 36px;
    height: 36px;
    margin: calc(0.75em - 17px) 0.25rem 0 0;
    vertical-align: top;
    border-radius : 4px;
}

.create_product form button{
    width: 100%;
    height: 40px;
    background: linear-gradient(to right, #000046, #1cb5e0);  
    color: white;
    height:42px;
    text-transform: uppercase;
    letter-spacing: 2px;
    font-weight: 700;
  border-radius: 10px;
}