.register-page{
    max-width: 500px;
    border: 2px solid #474747;
    border-radius: 5px;
    padding: 30px;
    margin: 20px auto;
}

.register-page h2{
    text-transform: uppercase;
    letter-spacing: 2px;
    color: #555;
}

.register-page form input,
.register-page form button{
    width: 100%;
    height: 40px;
    margin: 10px 0;
    padding: 0 5px;
 
}
.register-page .rowchecked input {
   
    width: 36px;
    height: 36px;
    margin: calc(0.75em - 17px) 0.25rem 0 0;
    vertical-align: top;
    border-radius : 4px;
}


.register-page form input:focus, textarea:focus{
    border-color: #C2DCFE;
    box-shadow: 0 0 1px rgba(229, 103, 23, 0.075)inset, 0 0 8px #9AC3FE;
    outline: 0 none;

}
.register-page form .row{
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.loginsubmit{
    width: 100%;
    border: transparent;
}




.register-page form button{
    
    background: linear-gradient(to right, #000046, #1cb5e0);  
    color: white;
    height:42px;
    text-transform: uppercase;
    letter-spacing: 2px;
    border-radius: 10px; 
}











/* TABLA */

.history-page{
    overflow-x: auto;
    padding-bottom: 30px;
}
.history-page h2, h4{
    text-align: center;
    margin: 15px;
    text-transform: uppercase;
    letter-spacing: 1px;
}
.history-page table{
    margin: auto;
    width: 100%;
}

.history-page table,tr,td{
    border-collapse: collapse;
}

.history-page table,th,tr{
    border: 1px solid #ddd;
}

th, td{
    text-align: center;
    padding: 8px;

}
table a{
    color: rgb(3, 165, 206);
    font-weight: bold;
}

tbody tr:nth-child(even) {

    background-color: #E5E5E5;

    }

 tbody tr:nth-child(odd) {

    background-color:#ffff;

    }



.history-page img{
    width: 70px;
    height: 100px;
    object-fit: cover;
}
.history-page button{
        width: 70px;
        height: 35px;
        color: white;

}

.btnedit{
    background: linear-gradient(0deg, #0992c4 0%, #04c872 100%);
    border-radius: 10px;
}
.btndelete{
    background:linear-gradient(90deg, #ca4834 0%, #d9183f 33%, #c42b47 74%);
    border-radius: 10px;
}