.login-page{
    max-width: 500px;
    border: 2px solid #474747;
    border-radius: 5px;
    padding: 30px;
    margin: 50px auto;
    text-align: center;
}
.login-page h2{
    text-transform: uppercase;
    letter-spacing: 2px;
    color: #555;
}
.login-page form input,
.login-page form button{
    width: 100%;
    height: 40px;
    margin: 10px 0;
    padding: 0 5px;
}

.login-page form input:focus, textarea:focus{
    border-color: #C2DCFE;
    box-shadow: 0 0 1px rgba(229, 103, 23, 0.075)inset, 0 0 8px #9AC3FE;
    outline: 0 none;

}


.login-page form .row{
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.loginsubmit{
    width: 100%;
    border: transparent;
}
.login-page form button{
    
    background: linear-gradient(to right, #000046, #1cb5e0);  
    color: white;
    text-transform: uppercase;
    letter-spacing: 2px;
    border-radius: 10px;
    height:42px;
}

